import React from 'react'
import homeImage from './images/homepageimage.jpg';
import LargeButton from '../LargeButton';
import CourseCard from '../CourseCard';

import {
    Container,
    Row,
    Col
} from "react-bootstrap";

export default function Home() {
    const margin = {
        marginTop: "150px"
    };
    return (
        <div> 
            <div className="text-center"><img src={ homeImage } alt="ref" className="w-75"></img></div>
            <Container>
                <h1 className="display-4 text-center my-5"><strong>L'Acadmie des arbitres de hockey vous souhaite la bienvenue!</strong></h1>
                <p className="lead my-5">L'Acadmie des arbitres de hockey accueille tous les officiels qui souhaitent apporter leur
                 officier des comptences au niveau suivant. L'Acadmie des arbitres de hockey propose trois cours diffrents qui sont
                 ddi  diffrentes classes de niveaux de comptence. Que vous dbutiez en tant que nouvelle ligue d'arbitrage,
                 dans un environnement comptitif ou en travaillant au hockey de haute performance, la HRA a une place pour vous. Si vous aimez ce que vous entendez,
                 nous vous encourageons  vous inscrire  l'un de nos cours via notre formulaire d'inscription en cliquant sur le bouton ci-dessous.
                </p>
               <LargeButton buttonText="S'inscrire maintenant!" link="/registerFR"/>
            </Container>
            
            <Container className="w-100" style={margin}>
                <h1 className="display-4 text-center my-5"><strong>Nos cours!</strong></h1>
                <Row className="w-100 my-5">
                    <Col>
                        <CourseCard title="Cours 1" subtitle="Officiels occasionnels" text="Ce cours est destin aux arbitres dbutants et occasionnels qui cherchent  apprendre les bases
                                 derrire l'arbitrage pour les prparer au hockey de comptition"/>
                    </Col>
                    <Col>
                        <CourseCard title="Cours 2" subtitle="Officiels comptitifs" text="Ce cours est destin aux officiels de comptition qui cherchent  apprendre des concepts avancs d'arbitrage
                                 pour les prparer  travailler au hockey de haute performance." />
                    </Col>
                    <Col>
                    <CourseCard title="Cours 3" subtitle="Officiels de haute performance" text="Ce cours est destin aux officiels de haut niveau qui cherchent  perfectionner leurs comptences pour
                                 niveaux les plus comptitifs pour les prparer  des niveaux professionnels" />
                    </Col>
                </Row>
                <LargeButton buttonText="Plus d'informations sur le cours" link="/coursesFR"/>
            </Container>

            <Container style={margin}>
                <h1 className="display-4 text-center my-5"><strong>Dcouvrez nos vidos gratuites!</strong></h1>
                <p className="lead my-5">Nous proposons des vidos de dveloppement gratuites pour tous les fonctionnaires souhaitant ajouter des conseils  leur bote  outils.
                 Nous avons divis les vidos en deux catgories "Arbitre" et "Juge de lignes" afin que vous puissiez facilement trouver des vidos pour les postes que vous souhaitez amliorer.
                 Nos vidos sont un excellent exemple de certains des concepts qui seront abords dans nos cours. Si vous aimez ce que vous voyez,
                 vous aurez plaisir  participer  l'un de nos cours. Vous pouvez visionner les vidos en cliquant sur le bouton ci-dessous.
                </p>
                <LargeButton buttonText="Regarde des vidos" link="/videosFR"/>
            </Container>

            <Container style={margin}>
                <h1 className="display-4 text-center my-5"><strong>Nous sommes des fonctionnaires comme vous!</strong></h1>
                <p className="lead my-5">Les instructeurs de la Hockey Referee Academy sont des officiels comme vous qui cherchent 
                 aider  amliorer la prochaine gnration de fonctionnaires grce  leur connaissance approfondie de l'emploi. Ils ont tous t dans le mme
                 position que vous et avez travaill dur pour devenir certains des meilleurs officiels du jeu. Nos instructeurs ont travaill
                 niveaux professionnels tels que OHL, WHL, AHL et NHL. Pour en savoir plus sur la Hockey Referee Academy ou si vous cherchez 
                 contactez-nous, vous pouvez utiliser le bouton correspondant ci-dessous.
                </p>
                <LargeButton buttonText="En savoir plus sur nous" link="/aboutFR"/>
                <LargeButton buttonText="Nous contacter" link="/contactusFR"/>
            </Container>
        </div>
    )
}
