import React from 'react'
import LargeButton from '../LargeButton';
import ProfileCard from '../ProfileCard';
import WesMcCauley from './images/mccauley.jpg';
import ChrisRooney from './images/rooney.jpg';
import KellySutherland from './images/sutherland.jpg';

import {
    Container,
    Row,
    Col
} from "react-bootstrap";

import {
    Link
} from "react-router-dom";

export default function About() {
    const margin = {
        marginTop: "150px"
    };
    return (
        <div>
            <Container>
                <h1 className="display-3 text-center mt-5"><strong> propos</strong></h1>
            </Container>

            <Container style={margin}>
                <h3 className="text-center display-4">Notre objectif</h3>
                <hr/>
                <p class="lead">Nous nous sommes fix comme objectif d'enseigner aux officiels afin de les aider  faire passer leur carrire d'officier au niveau suprieur.
                 Nous voulons former la prochaine gnration d'officiels afin qu'ils puissent continuer  offrir une exprience juste et amusante  ses joueurs et entraneurs
                 tout en s'amusant. Nous voulons voir tous nos fonctionnaires exceller dans leurs postes et potentiellement devenir un jour un professionnel
                 officiel.
                </p>
            </Container>

            <Container style={margin}>
                <h3 className="text-center display-4">Ce que nous faisons</h3>
                <hr/>
                <p class="lead"> Nous enseignons et formons nos officiels pour qu'ils deviennent les meilleurs officiels possibles.
                 Nous leur fournissons les connaissances et les comptences ncessaires pour faire passer leur carrire d'arbitre au niveau suprieur.
                 Nous enseignons aux officiels tous les aspects du travail afin qu'ils puissent avoir plus de contrle sur leurs jeux. Si vous souhaitez
                 en savoir plus sur ce que nous faisons, vous pouvez lire sur nos cours ou consulter nos vidos de dveloppement gratuites en utilisant les boutons
                 au dessous de.
                </p>
                <Container className="text-center">
                    <Link to="/CoursesFR" className="btn btn-lg btn-danger w-25 py-3 my-2 mr-5">Cours</Link>
                    <Link to="/videosFR" className="btn btn-lg btn-danger w-25 py-3 my-2">Vidos</Link>
                </Container>
            </Container>

            <Container style={margin}>
                <h3 className="text-center display-4">Qui nous sommes</h3>
                <hr/>
                <p class="lead"> Nous sommes un groupe d'officiels qui a travaill au hockey professionnel dans des ligues telles que l'OHL, la WHL, l'AHL et la LNH.
                 Nous sommes passionns par le fait de redonner au jeu et nous voulons donc apporter nos connaissances  la prochaine gnration d'officiels.
                 Nous nous engageons  fournir les meilleurs cours possibles pour amliorer nos fonctionnaires de la meilleure faon possible.
                 Si vous souhaitez en savoir plus sur nous, nos fiches de profil sont ci-dessous. Si vous souhaitez nous contacter, vous pouvez cliquer sur le bouton sous les fiches de profil.
                </p>
                <Container className="w-100">
                    <Row className="w-100 my-5">
                        <Col>
                            <ProfileCard name="Wes McCauley" bio="Bonjour, je suis Wes McCauley. J'effectue actuellement ma 18e saison comme arbitre dans la LNH." image={WesMcCauley} />
                        </Col>
                        <Col>
                            <ProfileCard name="Chris Rooney" bio="Bonjour, je suis Chris Rooney. Je suis actuellement en train d'arbitrer ma 20e saison comme arbitre dans la LNH" image={ChrisRooney} />
                        </Col>
                        <Col>
                            <ProfileCard name="Kelly Sutherland" bio="Bonjour, je suis Kelly Sutherland. Je suis actuellement en train d'arbitrer ma 15e saison comme arbitre dans la LNH" image={KellySutherland} />
                        </Col>
                    </Row>
                </Container>
                <LargeButton buttonText="Nous contacter" link="/contactusFR"/>
            </Container>
        </div>
    )
}
