import React from 'react'
import LargeButton from '../LargeButton';

import {
    Container
} from "react-bootstrap";

export default function Courses() {
    const margin = {
        marginTop: "100px"
    };
    return (
        <div>
            <Container>
                <h1 className="display-3 text-center my-5"><strong>Cours</strong></h1>
                <LargeButton buttonText="S'inscrire maintenant!" link="/registerFR"/>
            </Container>

            <Container style={margin}>
                <h1 className="display-4 text-center mt-5"><strong>Cours 1</strong></h1>
                <hr/>
                <p className="lead">Le cours 1 est offert aux officiels dbutants et aux officiels qui travaillent actuellement au hockey de ligue maison.
                 Nous recommandons galement ce cours aux fonctionnaires ayant 2 ans ou moins d'exprience.
                </p>
                <h3 className="mt-5">La description</h3>
                <p className="lead">Ce cours permettra aux officiels qui cherchent  arbitrer des niveaux comptitifs,  savoir AA / AAA d'amliorer leurs comptences
                 afin d'tre considr pour passer aux niveaux comptitifs. Le cours couvrira les principes fondamentaux du hockey officiel
                 ainsi que prsenter certains concepts importants ncessaires pour travailler les niveaux suprieurs. Le cours enseignera galement les concepts de routines hors glace
                 pour amliorer vos performances sur la glace.
                </p>
                <h3 className="mt-5">Ce que vous apprendrez</h3>
                <ul>
                    <li className="lead">Principes fondamentaux du positionnement</li>
                    <li className="lead">Rgles du jeu</li>
                    <li className="lead">Systmes  2 et 3 personnes</li>
                    <li className="lead">Routines d'avant-match / d'aprs-match</li>
                    <li className="lead">Et plus...</li>
                </ul>
            </Container>
            <Container style={margin}>
                <h1 className="display-4 text-center mt-5"><strong>Cours 2</strong></h1>
                <hr/>
                <p className="lead">Ce cours est recommand pour les officiels qui travaillent actuellement au hockey de comptition,  savoir le hockey AA / AAA.
                 Nous recommandons galement ce cours aux fonctionnaires qui ont 3  5 ans d'exprience.
                </p>
                <h3 className="mt-5">La description</h3>
                <p className="lead">Ce cours permettra aux officiels qui travaillent actuellement au hockey de comptition, notamment au hockey AA / AAA, d'affiner leurs comptences
                 afin d'tre considr pour passer aux niveaux de performance levs. Le cours couvrira les concepts avancs de l'arbitrage de hockey ainsi que prsenter
                 le systme  4 personnes. Le cours prsentera aux officiels la gestion du jeu et l'habilet de communiquer sur la glace pour dissiper toute situation qui pourrait survenir dans un environnement comptitif.
                </p>
                <h3 className="mt-5">Ce que vous apprendrez</h3>
                <ul>
                    <li className="lead">Technique de positionnement avance pour un environnement comptitif</li>
                    <li className="lead">Gestion du jeu</li>
                    <li className="lead">Systmes  3 et 4 personnes</li>
                    <li className="lead">Comptences en communication</li>
                    <li className="lead">Et plus...</li>
                </ul>
            </Container>
            <Container style={margin}>
                <h1 className="display-4 text-center mt-5"><strong>Cours 3</strong></h1>
                <hr/>
                <p className="lead">Ce cours est recommand aux officiels qui travaillent actuellement au hockey de haute performance,  savoir Midget AAA (U18) et
                 Junior A / B / C. Nous recommandons galement ce cours aux fonctionnaires qui ont plus de 6 ans d'exprience.</p>
                <h3 className="mt-5">La description</h3>
                <p className="lead">Ce cours permettra aux officiels qui travaillent actuellement au hockey de haute performance,  savoir Midget AAA et Junior A / B / C, de perfectionner leurs comptences.
                 afin de recevoir potentiellement une offre pour travailler au hockey professionnel. Ce cours couvrira les comptences avances ncessaires pour exceller dans un environnement de haute performance.
                 Les officiels en apprendront davantage sur la gestion des joueurs et sur la gestion du stress li  la haute performance. Les officiels
                 apprenez  grer les mles et les combats de manire approprie pour contrler leurs jeux.
                </p>
                <h3 className="mt-5">Ce que vous apprendrez</h3>
                <ul>
                    <li className="lead">Comptences de positionnement avances pour le hockey haute performance</li>
                    <li className="lead">Gestion des joueurs</li>
                    <li className="lead">Faire face au stress</li>
                    <li className="lead">Grer les mles et les combats</li>
                    <li className="lead">Et plus...</li>
                </ul>
            </Container>
            <Container className="mt-5">
                <LargeButton buttonText="S'inscrire maintenant!" link="/registerFR"/>
            </Container>
        </div>
    )
}
